.background-img
{
    background-image:url("https://i.ibb.co/BL0n3dT/food-wallpaper.png");
    height:50%;
    background-size:cover;
    background-repeat:no-repeat;
    margin:auto;
    border: solid;
    border-color: white;
}
.logo
{
    color:red;
    background-color:white;
    height:150px;
    width:150px;
    border-radius:50%;
    font-size:90px;
    margin-top:80px;
    margin-bottom:60px;
}
.heading
{
    color:white;
    font-size:36px;
}
.locationselector
{
    margin-top: 20px;
    margin-bottom: 40px;
    margin-left: 20px;

}
.locationdropdown
{
    height:50px;
    width: 500px;
    border-radius: 2%;
    border: none;
    font-size: 15px;
    margin-bottom:15px;
}
.searchrestaurents
{
    width:400px;
    height:40px;
    border-radius: 2%;
    border: none;
    font-size: 15px;
    color: grey;
    margin-bottom:15px;
}
